.open-keyboard-menu-button {
	width: 55px;
	height: 45px;
	margin: 100px 0 0 50px;
	color: #ffd700;
	font-size: 40px;
	background-color: #222222;
	border-radius: 7px;
}

.modal-keyboard-menu {
	position: absolute;
	bottom: 20%;
	left: 10%;
	.keyboard-settings-window {
		width: 480px;
		height: 285px;
		border-radius: 10px;
		background-color: #3a3a3a;
		box-shadow: 0 0 6px #ffd700;
		font-size: 10px;
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		padding: 20px 0;
		.keyboard-settings-button {
			width: 100px;
			height: 55px;
			display: flex;
			justify-content: space-around;
			align-items: center;
			background-color: #222222;
			border-radius: 7px;
			svg {
				color: white;
				font-size: 25px;
			}
			.key-button {
				width: 35px;
				height: 20px;
				font-size: 18px;
				font-weight: 600;
				color: black;
				background-color: #ffd700;
				border-radius: 5px;
				padding: 0 3px;
				box-sizing: border-box;
				&.active {
					box-shadow: 0 0 10px white;
				}
				&:hover {
					background-color: white;
				}
			}
		}
		.exit-button {
			position: absolute;
			top: 1px;
			right: 1px;
			width: 25px;
			height: 25px;
			border-radius: 10px;
			background-color: #ffd700;
			font-size: 23px;
		}
	}
}

.tools {
	width: 140px;
	display: flex;
	flex-direction: column;
	.tool-button {
		width: 50px;
		height: 50px;
		background-color: #3a3a3a;
		margin: 0 3px 3px 0;
		color: white;
		font-size: 25px;
		&:hover {
			background-color: #4f4f4f;
		}
		&.active {
			border: 3px solid #ffd700;
			box-sizing: border-box;
		}
	}
	.color-picker {
		width: 85px;
		height: 85px;
		position: relative;
		margin: 50px 0 0 10px;
		.swap-color-button {
			background-color: #282828;
			position: absolute;
			bottom: 5px;
			left: 7px;
			color: #d8d8d8;
			font-size: 20px;
			cursor: pointer;
		}
		.color-button {
			width: 50px;
			height: 50px;
			border: 4px solid #3a3a3a;
			;
			position: absolute;
			&.main {
				top: 0;
				left: 0;
				z-index: 1;
			}
			&.secondary {
				right: 0;
				bottom: 0;
			}
			input {
				visibility: hidden;
			}
			&:hover {
				border: 4px solid #4f4f4f;
			}
		}
	}
}