.landing-page {
	width: 100%;
	height: 94vh;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	background-color: #222222;
	.screenshot-container {
		width: 90vw;
		height: 400px;
		box-shadow: 0 0 6px #ffd700;
		display: flex;
		.main-screen-shoot {
			min-width: 1150px;
			height: 100%;
			background-image: url(../../assets/imgs/mainFox.png);
			background-size: cover;
			position: relative;
			overflow: hidden;
			.main-gif {
				position: absolute;
				left: 69.5%;
				top: 4vh;
				width: 235px;
				height: 235px;
				background-image: url(../../assets/imgs/fox.gif);
				background-size: cover;
			}
		}
		.information-container {
			width: 30vw;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			align-items: center;
			font-size: 1.1vw;
			letter-spacing: 2px;
			color: white;
			text-align: center;
			box-sizing: border-box;
			padding: 30px;
			background-color: #666666;
			.information-button-container {
				width: 100%;
				display: flex;
				justify-content: space-around;
				.information-container-button {
					width: 130px;
					height: 50px;
					background-color: #ffd700;
					border-radius: 5px;
					font-size: 18px;
					font-weight: 600;
					&:hover {
						background-color: white;
					}
				}
			}
		}
	}
	.tools-explanation-container {
		width: 89vw;
		height: 20vh;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		font-size: 1.0vw;
		color: white;
		.tool-explonation {
			width: 10vw;
			height: 4vw;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			box-shadow: 0 0 6px #ffd700;
			svg {
				font-size: 2.2vw;
				margin: 0 15px;
			}
		}
	}
	.author-information {
		color: #ffd700;
		font-size: 1.2vw;
		.author-link {
			svg {
				font-size: 1.7vw;
			}
			&:hover {
				color: white;
			}
		}
	}
}